import { useEffect, useState, useRef } from 'react';
import API from '../../api/API';
import { useTranslation } from 'react-i18next';
import { validChar, validEmail } from '../../global/globalValues';
import Select from 'react-select';
import { baseURL } from '../../global/globalValues';
import {CopyToClipboard} from 'react-copy-to-clipboard';

const Exchanger = () => {

    const selectSend = useRef(null);
    const selectGet = useRef(null);
    const { t } = useTranslation();
    const coins = [
        {
            id: 1,
            value: 'BTC',
            label: 'Bitcoin(BTC)',
            network: t('crypto.btc')
        },
        {
            id: 2,
            value: 'ETH',
            label: 'Ethereum(ETH)',
            network: t('crypto.eth')
        },
        {
            id: 3,
            value: 'XTZ',
            label: 'Tezos(XTZ)',
            network: t('crypto.xtz')
        },
        {
            id: 4,
            value: 'XLM',
            label: 'Stellar(XLM)',
            network: t('crypto.xlm')
        },
        {
            id: 5,
            value: 'DOGE',
            label: 'Dogecoin(Doge)',
            network: t('crypto.doge')
        },
        {
            id: 6,
            value: 'TRX',
            label: 'Tron(TRX)',
            network: t('crypto.trx')
        },
        {
            id: 7,
            value: 'XRP',
            label: 'Ripple(XRP)',
            network: t('crypto.xrp')
        },
        {
            id: 8,
            value: 'ADA',
            label: 'Cardano(ADA)',
            network: t('crypto.ada')
        },
        {
            id: 9,
            value: 'LTC',
            label: 'Litecoin(LTC)',
            network: t('crypto.ltc')
        },
    ];
    const coinsGet = [
        {
            id: 1,
            value: 'USDT',
            label: 'Tether'
        },
        {
            id: 2,
            value: 'USDC',
            label: 'USD coin'
        },
        {
            id: 3,
            value: 'BUSD',
            label: 'Binance USD'
        }
    ];
    const defSend = {
        value: 'BTC',
        label: 'Bitcoin',
        network: t('crypto.btc'),
        defaultValue: true
    };
    const defGet = {
        value: 'USDT',
        label: 'Tether',
        defaultValue: true
    };

    const [name, setName] = useState({
        nameSend: 'BTC',
        nameLabel: 'Bitcoin',
        network: t('crypto.btc'),
        nameGet: 'USDT',
    });

    const [tokenSend, setTokenSend] = useState({
        minAmount: 0.1,
        maxAmount: 3,
        wallet: "",
        value: 0,
    });

    const [amountSend, setAmountSend] = useState(0.1);
    const [amountGet, setAmountGet] = useState(0);
    const [isForward, setIsisForward] = useState(true);
    const [isBeck, setIsBeck] = useState(false);

    const chnageAmountSend = (e) => {
        setSend('none')
        if (validChar.test(e.target.value)) 
        setAmountSend(e.target.value)
        setIsisForward(true)
        setIsBeck(false)
    };

    const chnageAmountGet = (e) => {
        setSend('none')
        if (validChar.test(e.target.value)) 
        setAmountGet(e.target.value)
        setIsBeck(true)
        setIsisForward(false)
    }

    const [wallet, setWallet] = useState('');
    const [pay, setPay] = useState('none');
    const focusPayment = () => {
        setPay('flex')
        setSend('none')
    }
    const blurPayment = () => {
        setPay('none')
        setSend('none')
    }
    const changeWallet = (e) => {
        setSend('none')
        setWallet(e.target.value.replace(/[^a-z0-9]/gi, ''))
    }

    const [email, setEmail] = useState('');
    const [emailError, setEmailError] = useState('');
    const focusEmail = () => {
        setSend('none')
    }
    const blurEmail = () => {
        setSend('none')
    }
    const changeEmail = (e) => {
        setSend('none')
        setEmail(e.target.value)
        if (!validEmail.test(String(e.target.value).toLowerCase())){
            setEmailError(t('exchanger.invalidEmail'))
        } else {
            setEmailError('')
        }
    }

    const [telegram, setTelegram] = useState('');
    const otherContacts = () => {
        const t = document.getElementById('telegram')
        t.classList.toggle('active')
    }
    const changeTelegram = (e) => {
        setSend('none')
        setTelegram(e.target.value)
    }

    const [send, setSend] = useState('none');
    const [responce, setResponce] = useState('none');
    const [idResponce, setIdResponce] = useState(null);
    const sendVaulue = (e) => {
        e.preventDefault()
        if ( amountSend < tokenSend.minAmount 
            || amountSend > tokenSend.maxAmount 
            || emailError !== '' 
            || email === '' 
            || wallet === ''){
            setSend('flex')
        } else {
            setSend('none')
            let formData = new FormData();
            formData.append('from', name.nameSend);
            formData.append('to', name.nameGet);
            formData.append('network', name.network);
            formData.append('amount', amountSend);
            formData.append('result', amountGet);
            formData.append('wallet', wallet);
            formData.append('email', email);
            formData.append('telegram', telegram);
            API.postStatus(formData).then((data)=> {
                console.log(data)
                setIdResponce(data)
                setResponce('flex')
                document.body.classList.add('lock')
            });
        }
    }

    const requestAmount = async () => {
        return await API.getAmount(name.nameSend, name.nameGet).then(data => {
                setTokenSend({ ...tokenSend, 
                    minAmount: data.minAmount, 
                    maxAmount: data.maxAmount, 
                    wallet: data.wallet, 
                    value: data.value})
        });
    } 

    const requestToken = async () => {
            return await API.getToken( name.nameSend, name.nameGet, Number(amountSend), isForward ).then( data => {
                    setAmountGet(data.result)
            }).catch( ignore => {} );
    };

    const requestTokenBeck = async () => {
            return await API.getTokenBeck( name.nameGet, name.nameSend, Number(amountGet), isBeck ).then( data => {
                setAmountSend(data.result)
            }).catch( ignore => {} );
    }

    useEffect (() => {
        requestAmount()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [name.nameSend, name.nameGet, amountSend, amountGet]);

    useEffect(() => {
        requestToken();
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[name.nameSend, name.nameGet, amountSend, isForward]);

    useEffect(() => {
        requestTokenBeck();
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[name.nameSend, name.nameGet, amountGet, isBeck]);
    
    return (
        <div className='main__exchanger'>
            <form className='exchanger__form' onSubmit={e => sendVaulue(e)}>

                {
                    amountSend < tokenSend.minAmount 
                    ? <div className='exchanger__sent-amount' style={{color:`#ff0000`}}>{t('exchanger.minAmount')} {tokenSend.minAmount}</div>
                    : <div className='exchanger__sent-amount' style={{color:`#ff8000`}}>{t('exchanger.maxAmount')} {tokenSend.maxAmount}</div>
                }

                <div className='exchanger__sent'>

                    <div className='exchanger__sent-control'>
                        <span>{t('exchanger.send')}</span>
                        <input 
                            type="text" 
                            name='amount'
                            title={t('exchanger.title')} 
                            inputMode='demicial'
                            placeholder={t('exchanger.enter')}
                            maxLength={22}
                            value = {amountSend}
                            onChange={e => chnageAmountSend(e)}
                        />
                    </div>

                    <div 
                    style={{backgroundImage:`url(${baseURL}/resources/images/${name.nameSend}.svg)`}}
                    className='exchanger__img-sent'
                    ></div>

                    <Select 
                        ref={selectSend}
                        name='from'
                        className='exchanger__select'
                        options={coins}
                        defaultValue={defSend}
                        onChange={e => setName({...name, nameSend: e.value, network: e.network, nameLabel: e.label})}
                    />
                </div>

                <div className='crypto__value'>{t('exchanger.CryptoValue')}{name.nameSend} = {tokenSend.value} $</div>

                <div className='exchanger__sent'>

                    <div className='exchanger__sent-control'>
                        <span>{t('exchanger.get')}</span>
                        <input 
                            name='result'
                            type="text" 
                            title={t('exchanger.title')} 
                            placeholder="-"
                            // readOnly
                            onChange={ e => chnageAmountGet (e)} 
                            value={amountGet} 
                        />
                    </div>

                    <div 
                    style={{backgroundImage:`url(${baseURL}/resources/images/${name.nameGet}.svg)`}}
                    className='exchanger__img-get'
                    ></div>

                    <Select 
                        ref={selectGet} 
                        name='to'
                        className='exchanger__select'
                        options={coinsGet}
                        defaultValue={defGet}
                        onChange={e => setName({...name, nameGet: e.value })}
                    />
                </div>

                    <span className='exchanger__title-span'>{t('exchanger.wallet')}</span>
                    <div className='exchanger__payment'>
                        <input
                            type='text'
                            name='wallet'
                            title={t('exchanger.titlePayment')}
                            placeholder={t('exchanger.payout')}
                            value={wallet}
                            onChange={e => {changeWallet(e)}}
                            onFocus={focusPayment}
                            onBlur={blurPayment}
                        />
                        <div style={{display: pay}} className='payment__info'>{t('exchanger.paymentInfo')}</div>
                    </div>

                    <span className='exchanger__title-span'>{t('exchanger.email')}</span>
                    <div className='exchanger__email'>
                        <input
                            type='text'
                            name='email'
                            title={t('exchanger.titleEmail')}
                            placeholder='example@gmail.com'
                            value={email}
                            onChange={e => {changeEmail(e)}}
                            onFocus={focusEmail}
                            onBlur={blurEmail}
                        />
                        {emailError && 
                        <div className='email__error'> {emailError} </div>
                        }
                    </div>

                <div className='exchanger__button'>
                    <button type='submit' >{t('exchanger.button')}</button>
                    <div style={{display: send}} className='send__info'>{t('exchanger.sendInfo')}</div>
                </div>

                    <span className='exchanger__span' onClick={otherContacts}>{t('exchanger.otherContacts')}</span>
                    <div id='telegram' className='telegram__wrapper'>
                        <span className='exchanger__title-span'>Telegram:</span>
                        <div  className='telegram__input'>
                            <input
                            type='text'
                            name='telegram'
                            title={t('exchanger.titleTelegram')}
                            placeholder='@example'
                            value={telegram}
                            onChange={e => {changeTelegram(e)}}
                            />
                        </div>
                    </div>
            </form>

            <div style={{display: responce}} className='exchanger__responce'>
                <form className='responce__content'>

                <div className='responce__tittle'>{t('responce.confirm')}</div>

                <div className='responce__id'>
                    <span>ID:{idResponce}</span>
                </div>

                <div className='responce__info'>
                    <div className='responce__data'>
                        <div className='data__info'>
                            <span>{t('responce.youSend')}</span>
                            <br/>
                            <span className='data__info-value'>
                                {amountSend} {name.nameSend}
                            </span>
                        </div>

                        <div className='data__info'>
                            <span>{t('responce.youGet')}</span>
                            <br/>
                            <span className='data__info-value'>
                                {amountGet} {name.nameGet}
                            </span>
                        </div>
                        <div className='data__info'>
                            <br/>
                            {t('crypto.responce')}
                            <br/>
                            <span className='data__info-value'>
                                {name.nameLabel} - ({name.network})
                            </span>
                        </div>
                    </div>

                    <div className='responce__wallet'>
                        <span>{t('responce.toAdress')}</span>
                        <div className='responce__wallet-number'>{tokenSend.wallet}</div>
                        <CopyToClipboard text={tokenSend.wallet}>
                            <div className='copy'></div>
                        </CopyToClipboard>
                    </div>

                    <div className='responce__loader'>
                        <div></div>
                    </div>
                </div>

                    <div className='responce__button'>
                        <button type='submit'>{t('exchanger.buttonPay')}</button>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default Exchanger;