export const baseURL = '';
export const validChar = /^[.]?\d*(?:[.]\d*)?$/;
export const validWallet = /^(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+)$/
export const validEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;