import {Link} from "react-router-dom";
import cl from './Header.module.css';
import i18n from 'i18next';
import { useTranslation } from "react-i18next";
import {useState, useRef} from 'react';
import { useOutside } from "../../../hooks/useOutside";

const Header = () => {
    
    const outsideRef = useRef(null);
    const [modal, setModal] = useState('none');
    const [lang, setLang] = useState('en');
    const { t } = useTranslation();
    const selectLeng = [cl.selected__lang];

    
    const changeLang = (e) => {
        i18n.changeLanguage(e)
        setLang(e)
        setModal('none')
    }

    if (lang === 'en') {
        selectLeng.push(cl.en)
    } else {
        selectLeng.push(cl.ru)
    };

    const activeModal = () => {
        setModal('flex')
    };

    const tog = document.getElementById('toggleRef');
    const togled = document.getElementById('togledRef');
    const toggleRef =useRef(null);
    const togledRef =useRef(null);
    const activeBurger = () => {
        tog.classList.toggle('active');
        togled.classList.toggle('active');
}
    const scrollTop = e => {
        e.preventDefault()
        toggleRef.current.classList.toggle('active')
        togledRef.current.classList.toggle('active')
        window.scroll({
            top: 0,
            behavior: 'smooth'
        })
    }
    const scrollBottom = e => {
        e.preventDefault()
        toggleRef.current.classList.toggle('active')
        togledRef.current.classList.toggle('active')
        window.scroll({
            top: 820,
            behavior: 'smooth'
        })
    }

    useOutside (outsideRef, setModal)

    return (
            <header className={cl.content}>
                <div className={cl.container}>
                    <div className={cl.body}>
                        <Link to='/main' className={cl.logo__link}>
                            <div className={cl.logo}></div>
                            <div className={cl.logo__text}>
                                <span>Crypto</span>
                                Exchanger
                            </div>
                        </Link>

                        <div ref={outsideRef} className={cl.menu}>

                            <nav ref={togledRef} id="togledRef" className={cl.burg__body}>
                                <ul className={cl.burg__list}>
                                    <li>
                                        <a 
                                        onClick={scrollTop}
                                        className={cl.burg__link} 
                                        href='#m'
                                        >{t('title.main')}
                                        </a>
                                    </li>
                                    <li>
                                        <a 
                                        onClick={scrollBottom}
                                        className={cl.burg__link} 
                                        href='#m'
                                        >{t('title.about')}
                                        </a>
                                    </li>
                                    <li>
                                    </li>
                                </ul>
                            </nav>
                            <div className={cl.lang__menu}>
                            <div 
                                className={selectLeng.join(' ')}
                                onClick = {activeModal}>
                                </div>

                                <ul 
                                style={{display: modal}}
                                className={cl.selected__list}
                                >
                                    <li

                                    className={cl.en}
                                    onClick={e => changeLang("en")}
                                    >
                                    </li>
                                    <li 

                                    className={cl.ru}
                                    onClick={e => changeLang("ru")}
                                    >
                                    </li>
                                </ul>
                            </div>
                            

                            <div ref={toggleRef} id="toggleRef" onClick={activeBurger} className={cl.burg__tog}>
                                    <span></span>
                            </div>

                        </div>
                    </div>
                </div>
                    
            </header>
    );
};

export default Header;